@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=MontSerrat:400,500,700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px MontSerrat, sans-serif;
  background: #F5F5F5;
  -webkit-font-smoothing: antialiased;
}

input, button, textarea {
  font: 400 18px MontSerrat, sans-serif;
}

button {
  cursor: pointer;
}

form input {
  width: 100%;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 0 24px;
}

form textarea {
  width: 100%;
  min-height: 140px;
  resize: vertical;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 16px 24px;
  line-height: 24px;
}

.button {
  width: 100%;
  height: 45px;
  background: #00E676;
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-weight: 700;
  margin-top: 16px;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
 
  transition: -webkit-filter 0.2s;
 
  transition: filter 0.2s;
 
  transition: filter 0.2s, -webkit-filter 0.2s;
  
}
.button-outline {
  width: 100%;
  height: 45px;
  background: #fff;
  border: 0;
  border-radius: 8px;
  color: #00E676;
  font-weight: 700;
  margin-top: 16px;
  text-align: center;
  text-decoration: none;
  font-size: 18px;

  border: 0.5px solid#00E676;
  
  
 
  transition: -webkit-filter 0.2s;
  
  
 
  transition: filter 0.2s;
  
  
 
  transition: filter 0.2s, -webkit-filter 0.2s;
  
}

.button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.back-link {
  display: flex;
  align-items: center;
  margin-top: 40px;
  color: #41414d;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.2s;
}

.back-link:hover {
  opacity: 0.8;
}

.back-link svg {
  margin-right: 8px;
}

.logon-container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #00E676;
  flex-direction: row;
  overflow: hidden !important;
}

.logon-container section.form {
  width: 100%;
  max-width: 350px;
  background-color: white;
  z-index: 999;
  padding: 28px;
  border-radius: 8px;
  align-items: center;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.logon-container section.form form {
  margin-top: 15px;
}

.logon-container section.form form h1 {
  font-size: 32px;
  margin-bottom: 32px;
}

.circle-image {
  height: 150%;
  left: 50%;
  position: absolute;
  background-size: 'cover';
  background-repeat: no-repeat;
  overflow: hidden;
}

.logo-img {
  width: 80%;
  height: 80%;
  display: flex;
  align-self: center;
}

.title-login{
  text-align: center;
  margin-top: 10px;
  margin-bottom: 15px;
  color: #00E676;
  font-weight: 200;
}

.input-login {
  height: 50px;
  border-width: 0px;
  background-color: #F5F5F5;
  margin-top: 8px;
  margin-bottom: 8px;
}

/* Firefox, Chrome, Opera */
::-webkit-input-placeholder {
  color: #9E9E9E;
}
:-ms-input-placeholder {
  color: #9E9E9E;
}
::-ms-input-placeholder {
  color: #9E9E9E;
}
::placeholder {
  color: #9E9E9E;
}

.text-register{
  text-align: center;
  margin-top: 26px;
  margin-bottom: -5px;
  color: #00E676;
}

.forgot-password{
  text-align: center;
  margin-top: 26px;
  margin-bottom: -5px;
  color: #00E676;
  text-decoration: underline;
  cursor: pointer;
}

.profile-container {
  width: 100%;
  max-width: 1180px;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  background-color: #F5F5F5;
  justify-content: space-between;
 
}

.profile-container header {
  display: flex;
  align-items: center;
}

.profile-container header span {
  font-size: 20px;
  margin-left: 24px;
}

.profile-container header img {
  height: 64px;
}

.profile-container header a {
  width: 260px;
  margin-left: auto;
  margin-top: 0;
}

.profile-container header button {
  width: 60px;
  height: 60px;
  border-radius: 4px;
  border: 1px solid #dcdce6;
  background-attachment: transparent;
  margin-left: 16px;
  transition: border-color 0.2s;
}

.profile-container header button:hover {
  border-color: #999;
}

.profile-container h1 {
  margin-top: 80px;
  margin-bottom: 24px;
}

.profile-container ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 24px;
  list-style: none;
}

.profile-container ul li {
  background: #FFF;
  padding: 24px;
  border-radius: 8px;
  position: relative;
}

.profile-container ul li button {
  position: absolute;
  right: 24px;
  top: 24px;
  border: 0;
}

.profile-container ul li button:hover {
  opacity: 0.8;
}

.profile-container ul li strong {
  display: block;
  margin-bottom: 16px;
  color: #41414d;
}

.profile-container ul li p + strong {
  margin-top: 32px;
}

.profile-container ul li p {
  color: #737380;
  line-height: 21px;
  font-size: 16px;
}

.logo-img {
  width: 70%;
  height: 70%;
  display: flex;
  align-self: center;
}

.register-container {
  width: 100%;
  max-width: 1120px;
  height: 100vh;
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;
}

.register-container .content {
  width: 100%;
  padding: 96px;
  background: #f0f0f5;
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
  border-radius: 0px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.register-container .content section {
  width: 100%;
  max-width: 380px;
}

.register-container .content section h1 {
  margin: 64px 0 32px;
  font-size: 32px;
}

.register-container .content section p {
  font-size: 18px;
  color: #737380;
  line-height: 32px;
}

.register-container .content form {
  width: 100%;
  max-width: 450px;
}

.register-container .content form input {
  margin-top: 8px;
}

.register-container .content form .input-group {
  display: flex;
}

.register-container .content form .input-group input + input {
  margin-left: 8px;
}

